import { Row, Col, Button } from "reactstrap";
import { useState } from "react";
import Select from "antd/lib/select";
import message from "antd/lib/message";
import Modal from "antd/lib/modal";

import wechat from "./assets/wechat.jpeg";
import wechatInfo from "./assets/wechat-info.jpg";
import wechatIcon from "./assets/wechat-icon.png";
import zhihu from "./assets/zhihu.png";
import redbook from "./assets/redbook.png";
import bili from "./assets/bilibili.svg";
import weibo from "./assets/weibo.png";
import douban from "./assets/douban.png";
import douyin from "./assets/douyin.png";
import kuaishou from "./assets/kuaishou.png";
import hupu from "./assets/hupu.svg";

import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'antd/es/select/style/css';
import 'antd/es/message/style/css';
import 'antd/es/modal/style/css';

const { Option } = Select;

function App() {
  const [types, setTypes] = useState([
    {
      title: '',
      id: 'yunmeng-zhihu',
      option: [
        { label: '', value: '1.5/' },
        { label: '/', value: '4/' },
        { label: '/', value: '5/' },
        { label: '/', value: '6/' },
        { label: '/', value: '7/' },
        { label: '/', value: '8/' },
        { label: '/', value: '12/' },
        { label: '', value: '15' },
        { label: '', value: '20' },
        { label: '', value: '40' },
        { label: '', value: '50' },
        { label: '', value: '140/' },
        { label: '', value: '160/' },
        { label: '', value: '' },
        { label: '', value: '' },
        { label: '', value: '' },
        { label: '', value: '100/1000' },
        { label: '', value: '' },
      ]
    },
    {
      title: '',
      id: 'yuunmeng-redbook',
      option: [
        { label: '', value: '1/' },
        { label: '', value: '1.5/' },
        { label: '', value: '1/' }
      ]
    }
  ]);

  const [defaultIndex, setIndex] = useState(0);
  const [showModal, setShowModal] = useState(false);

  const copyToClip = (val) => {
    var aux = document.createElement("input");
    aux.setAttribute("value", val);
    document.body.appendChild(aux);
    aux.select();
    document.execCommand("copy");
    document.body.removeChild(aux);
    message.success('')
  }

  return (
    <Col md={6} sm={12} xs={12} className="m-auto">
      <div className='yunmeng position-relative'>
        <div className="header position-relative w-100 text-white d-flex flex-column align-items-center justify-content-center">
          <Col sm={11} xs={11} md={11} className="p-3">
            <h2></h2>
            <h5></h5>
          </Col>
        </div>
        <Col sm={11} xs={11} className="bg-white m-auto wechat d-flex flex-row justify-content-between align-items-center p-3 position-relative">
          <div className="flex-fill d-flex flex-row">
            <div className="wechat-qr">
              <img src={wechat} alt="" onClick={() => setShowModal(true)} />
            </div>
            <div className="d-flex flex-column justify-content-around ms-3">
              <div><img className="wechat-icon" src={wechatIcon} alt="" /><b>XXXXL0203</b></div>
              <small>24</small>
            </div>
          </div>
          <div>
            <Button
              color="info"
              outline
              size="sm"
              onClick={() => {
                copyToClip('XXXXL0203')
              }}
            >
              
            </Button>
          </div>
        </Col>
        <Col className="m-auto p-3">
          <h3 className="mb-3"></h3>
          <div className="business border p-3">
            <Row className="ps-5 pe-5 justify-content-around align-items-center">
              <img src={zhihu} alt="" style={{ width: "100px" }} />
              <img src={redbook} alt="" style={{ width: "100px" }} />
            </Row>
            <Row className=" justify-content-around align-items-center">
              <img src={bili} alt="" style={{ width: "100px", color: "#00AEEC" }} />
              <img src={weibo} alt="" style={{ width: "100px" }} />
              <img src={douban} alt="" style={{ width: "100px" }} />
            </Row> <Row className=" justify-content-around align-items-center">
              <img src={douyin} alt="" style={{ width: "100px" }} />
              <img src={kuaishou} alt="" style={{ width: "100px" }} />
              <img src={hupu} alt="" style={{ width: "60px", marginRight: "10px", background: "#F00000" }} />
            </Row>
          </div>
          <h3 className="mt-3 mb-3"></h3>
          <div className="min-vh-100">
            <Select defaultValue={defaultIndex} className="yunmeng-select" size="large"
              style={{ fontSize: '20px' }}
              onChange={(i) => {
                setIndex(i);
              }}
            >
              {
                types.map((t, index) => <Option key={index} value={index}>{t.title}</Option>)
              }
            </Select>
            <div className="mt-3">
              <ul className="p-0">
                {
                  types[defaultIndex].option.map((o, index) => <li key={index} className="w-100 d-flex flex-row justify-content-between"
                    style={{ fontSize: '16px', margin: '4px 0px' }}
                  >
                    <div className="text-left">{o.label}</div>
                    <div className="text-right" style={{ color: 'black', fontWeight: 'bolder' }}>{o.value}</div>
                  </li>)
                }
              </ul>
            </div>
          </div>
        </Col>
      </div>
      <Modal
        title=""
        centered
        visible={showModal}
        onOk={() => setShowModal(true)}
        onCancel={() => setShowModal(false)}
      >
        <img src={wechatInfo} alt="" style={{ width: '100%', display: 'block' }} />
      </Modal>
      <Col xs={12} sm={12} md={6} className="create-prder m-auto">
        <Button color="primary" block size="lg"
          onClick={() => {
            setShowModal(true)
          }}
        ></Button>
      </Col>
    </Col >
  );
}

export default App;
